/*About container*/
.aboutContainer {
  grid-column: 1 / span 5;
  grid-row: 1 / 3;
  text-align: center;
  padding: 2vh;
  color: #14213d;
}
.aboutHeader {
  text-shadow: 2px 2px #14213d;
  font-weight: bold;
  color: #fca311;
  font-size: 2em;
  margin: 1vh;
}

.aboutContainer p {
  margin: 1vh;
}

.aboutText {
  font-size: 1em;
  padding-left: 1em;
  padding-right: 1em;
}

.picOfMe {
  width: 40%;
  border-radius: 20px;
}

.downloadButton {
  background: rgb(52, 58, 62);
  color: white;
  border: none;
  box-shadow: 3px 3px 7px -1px rgba(20, 20, 31, 0.5);
  transform: translateY(-1px) translateX(-1px);
  border-radius: 15px;
  font-size: 1.25em;
  font-weight: bold;
  padding: 0.25em 1em;
  cursor: pointer;
  margin-top: 0.5em;
  margin-bottom: 0.5em;
  transition: 0.1s ease-in-out;
}

.downloadButton:hover {
  box-shadow: none;
  transform: none;
  color: white;
}

.downloadButton:active {
  color: #fca311
}

/*Fade in*/
.fade-in-about {
  opacity: 0;
  transform: translateY(20vh);
  visibility: hidden;
  transition: opacity 0.6s ease-out, transform 0.5s ease-out;
  will-change: opacity, visibility;
}
.fade-in-about.is-visible {
  opacity: 1;
  transform: none;
  visibility: visible;
}

@media only screen and (max-width: 1000px) {
  .aboutContainer {
    grid-column: 1 / span 12;
  }
}
@media only screen and (max-width: 550px) {
  .picOfMe {
    width: 50%;
  }
}
