@import url(https://fonts.googleapis.com/css2?family=Ubuntu);
html {
  max-width: 100vw;
  overflow-x: hidden;
  box-sizing: border-box;
  font-size: 16px;
  scroll-behavior: smooth;
}

body {
  max-width: 100vw;
  overflow: auto;
}

*,
*:before,
*:after {
  box-sizing: inherit;
}

a {
  color: white;
}

a:visited { color:white; }

a:hover a:active { color: #fca311}

body,
h1,
h2,
h3,
h4,
h5,
h6,
p,
ol,
ul {
  margin: 0;
  padding: 0;
  font-weight: normal;
}

ol,
ul {
  list-style: none;
}

img {
  max-width: 100%;
  height: auto;
}

body {
  background: #e5e5e5;
  font-family: Verdana, Geneva, Tahoma, sans-serif;
}

.container{
  display: grid;
  padding-top: 2em;
  grid-template-columns: repeat(12, 1fr);
  background-color: #e5e5e5;
  overflow-x: hidden;
}

@media only screen and (min-width:2000px) {
  .projects {
    margin-right: 30%
  }
  .aboutContainer {
    margin-left: 30%
  }
  
  .workContainer {
    grid-column: 3 / 6;
  }

  .eduContainer {
    grid-column: 7 / 10
  }
}

/*HEADER AND IT'S CONTENTS*/

header {
  grid-column: 1 / span 12;
  font-family: Ubuntu, sans-serif;

  overflow: hidden;
  color: #d8d7d4;
  display: flex;
  flex-flow: column nowrap;
  height: 100vh;
  text-align: center;
}

header::before {
  background: url("/img/MyPic-min.jpg");
  background-blend-mode: saturation;
  background-repeat: no-repeat;
  background-position: center;
  background-size: cover;
  content: '';
  height: 100%;
  left: 0;
  position: fixed;
  top: 0;
  width: 100%;
  will-change: transform;
  z-index: -1;
}


.titleAndSubtitle {
  -webkit-filter: none;
          filter: none;
  text-align: left;
  margin: 4vw;
  padding: 0 1.5em 1.5em 1.5em;
  margin-top: auto;
  margin-bottom: auto;
  word-wrap: break-word;

  max-width: 33%;
  border-radius: 30px;
}

.heroLink {
  margin: 0.5em;
}

.navlink > .emoji {
  margin-right: 5px;
}

.heroLink  img{
  width: 2.5em;
}

.title {
  text-shadow: 2px 2px 2px black;
  color: #f4f3ee;
  font-size: 4em;
}

.subtitle {
  color: #fca311;
  text-shadow: 2px 2px 2px black;
  margin-top: 0.5em;
  margin-bottom: 0.5em;
}

nav {
  position: fixed;
  display: flex;
  z-index: 1;
  flex-flow: row nowrap;
  justify-content: space-evenly;
  width: 100vw;
}

.navlink {
  background-color: rgb(52, 58, 62);
  color: #fca311;
  width: 20%;
  text-decoration: none;
  font-size: 1.3em;
  padding-top: 0.3em;
  padding-bottom: 0.3em;
  text-align: center;
  transition: 0.3s ease-in-out;
  border:none;
}

.navlink:not(:last-child) {
  border-right: none;
}

.navlink:hover {
  background-color: #fca311;
  color: #e5e5e5;
  width: 25%;
}

.navlink:active {
  background-color: white;
  color: #454140;
  transition: 0s;
}

/*Fade-in*/
.fade-in-hero {
  opacity: 0;
  transform: translateX(-20vh);
  visibility: hidden;
  transition: opacity 0.6s ease-out, transform 1.2s ease-out;
  will-change: opacity, visibility;
}
.fade-in-hero.is-visible {
  opacity: 1;
  transform: none;
  visibility: visible;
}

@media only screen and (max-width: 1000px) {
  header {
    background-position: 50%;
  }

  .titleAndSubtitle {
    padding: 0.1em 1em 1em 1em;
    max-width: 100%;
    background: none;
  }

  .navlink:hover {
    width: 20%;
  }

  .heroLinkPic {
    max-width: 2.5em;
  }
}

@media only screen and (max-width: 550px) {
  header {
    height: 55vh;
  }
  
  header::before {
    background-position: 60% -20vh;
  }

  .titleAndSubtitle {
    margin-top: auto;
  }

  .navlink {
    font-size: 1.3em;
    padding-left: 0;
    padding-right: 0;
  }

  .navText {
    display: none;
  }

  .emoji {
    margin: 0
  }

  .title {
    font-weight: bold;
    font-size: 2em;
  }

  .subtitle {
    font-weight: bold;
    font-size: 1.1em;
  }

}
@media only screen and (max-height: 500px) {
  header {
    height: 100vh;
  }
}

/*About container*/
.aboutContainer {
  grid-column: 1 / span 5;
  grid-row: 1 / 3;
  text-align: center;
  padding: 2vh;
  color: #14213d;
}
.aboutHeader {
  text-shadow: 2px 2px #14213d;
  font-weight: bold;
  color: #fca311;
  font-size: 2em;
  margin: 1vh;
}

.aboutContainer p {
  margin: 1vh;
}

.aboutText {
  font-size: 1em;
  padding-left: 1em;
  padding-right: 1em;
}

.picOfMe {
  width: 40%;
  border-radius: 20px;
}

.downloadButton {
  background: rgb(52, 58, 62);
  color: white;
  border: none;
  box-shadow: 3px 3px 7px -1px rgba(20, 20, 31, 0.5);
  transform: translateY(-1px) translateX(-1px);
  border-radius: 15px;
  font-size: 1.25em;
  font-weight: bold;
  padding: 0.25em 1em;
  cursor: pointer;
  margin-top: 0.5em;
  margin-bottom: 0.5em;
  transition: 0.1s ease-in-out;
}

.downloadButton:hover {
  box-shadow: none;
  transform: none;
  color: white;
}

.downloadButton:active {
  color: #fca311
}

/*Fade in*/
.fade-in-about {
  opacity: 0;
  transform: translateY(20vh);
  visibility: hidden;
  transition: opacity 0.6s ease-out, transform 0.5s ease-out;
  will-change: opacity, visibility;
}
.fade-in-about.is-visible {
  opacity: 1;
  transform: none;
  visibility: visible;
}

@media only screen and (max-width: 1000px) {
  .aboutContainer {
    grid-column: 1 / span 12;
  }
}
@media only screen and (max-width: 550px) {
  .picOfMe {
    width: 50%;
  }
}

.outerProject {
  perspective: 1000px;
}

.innerProject {
  position: relative;
  width: 100%;
  max-height: 100%;
  transform-style: preserve-3d;
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  background-color: rgb(52, 58, 65);
  color: white;
  box-shadow: 2px 2px 4px black;
  border: solid 0px black;
  border-radius: 5px;
}

.description {
  max-height: 100%;
  overflow: auto;
}

.projectFront {
  position: relative;
  width: 100%;
  height: 100%;
  -webkit-backface-visibility: hidden; /* Safari */
  backface-visibility: hidden;
}

.projectBack {
  position: absolute;
  width: 100%;
  height: 100%;
  -webkit-backface-visibility: hidden; /* Safari */
  backface-visibility: hidden;
  border-radius: 5px;
}

.innerProject:hover {
  background-color: #19395e;
  color: white;
  transition: 0.2s;
}

.projectBack {
  transform: rotateY(180deg);
  background: rgb(52, 58, 65);
  padding: 1em;
  padding-bottom: 4em;
}

.projectname {
  font-family: Ubuntu, Verdana, Geneva, Tahoma, sans-serif;
  font-weight: bold;
  justify-self: flex-start;
  text-decoration: underline;
  display: block;
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
  margin-bottom: 1em;
  font-size: 1.2em;
  transition: 0.1s ease-in-out;
}

.projectname:hover {
  color: #fca311
}

.projectname:visited {
  color: white;
}

.expandArrow {
  z-index: 2;
  cursor: pointer;
  width: 1.75em;
  align-self: center;
  border-radius: 100vw;
  margin-bottom: 1em;
  padding: 2px;
  transition: 0.2s;
}

.expandArrow:hover {
  background: #f3f3f3;
  box-shadow: #f3f3f3 0px 0px 10px;

  transition: 0.2s;
}

.projectThumbnail {
  object-fit: contain;
  display: block;
  height: 5em;
  margin: 1em auto 1em auto;
}

.outerProject.open .innerProject {
  transform: rotateY(180deg);
}

.fade-in-project {
  opacity: 0.3;
  visibility: hidden;
  transition: opacity 0.3s ease-out, transform 0.8s ease-out;
  will-change: opacity, visibility;
}

.fade-in-project.is-visible {
  opacity: 1;
  transform: none;
  visibility: visible;
  transition: 0.5s;
  will-change: visibility, opacity;
}

@media only screen and (max-width: 1000px) {
  .innerProject {
    width: 100%;
  }

  .projectname {
    font-size: 1.3em;
  }

  .projectThumbnail {
    width: 40%;
  }

  .description {
    font-size: 1em;
    margin-bottom: 0em;
    text-overflow: ellipsis;
  }

  .expandArrow {
    width: 2em;
  }
}

@media only screen and (max-width: 550px) {
  .innerProject {
    min-width: 80vw;
    height: 100%;
  }
}

/*Projects*/
.projects {
  text-align: center;
  grid-column: 6 / span 12;
  grid-row: auto;
  margin-left: auto;
  margin-right: auto;
  padding-bottom: 2vh;

  width: -webkit-max-content;

  width: -moz-max-content;

  width: max-content;
  min-width: 80%;
}

.projectsGrid {
  display: grid;
  grid-auto-flow: row dense;
  overflow-y: hidden;
  grid-template: 1fr 1fr / 1fr 1fr 1fr;
  grid-gap: 0.75em;
  padding: 0.5em;
  color: #d8d7d4;
}

.projectsHeader {
  text-shadow: 2px 2px #14213d;
  font-weight: bold;
  color: #fca311;
  font-size: 2em;
  margin: 1vh;
}

/*Fade-in*/
.fade-in-projects {
  opacity: 0;
  transform: translateX(20vh);
  visibility: hidden;
  transition: opacity 0.6s ease-out, transform 0.5s ease-out;
  will-change: opacity, visibility;
}
.fade-in-projects.is-visible {
  opacity: 1;
  transform: none;
  visibility: visible;
}

@media only screen and (max-width: 550px) {
  .projectsGrid {
    grid-auto-flow: column dense;
    grid-template-rows: 1fr;
    overflow-x: auto;
    height: auto;
  }

  .projectsGrid .outerProject:last-child {
    padding-right: 0.75em;
  }
}

@media only screen and (max-width: 1000px) {
  .projects {
    grid-column: 1 / span 12;
    overflow-x: hidden;
    max-width: 100%;
  }
}

.job {
  text-align: center;
  margin: 1em;
  color: #fca311;
  background: rgb(52, 58, 65);
  border-radius: 15px;
  box-shadow: 3px 3px 3px black;
}

.jobTitle {
  padding: 0.75em;
  background: #fca311;
  color: #14213d;
  border-radius: 15px;
  max-width: 100%;
}

.jobCompany {
  border: 2px solid #fca311;
  padding: 0.25em 0.75em;
  margin: 0.25em auto 0.25em auto;
  border-radius: 50px;
  width: -webkit-max-content;
  width: -moz-max-content;
  width: max-content;
}

.jobStartAndEnd {
  color: white;
  padding: 0.5em;
  display: flex;
  justify-content: space-around;
}

.fade-in-job {
  opacity: 0;
  transform: translateX(-20vh);
  visibility: hidden;
  transition: opacity 0.6s ease-out, transform 0.25s ease-out;
  will-change: opacity, visibility;
}

.edu {
  background: #fca311;
}

.edu > .jobTitle {
  background: rgb(52, 58, 65);
  color: white;
}

.edu > .jobCompany {
  border-color: black;
  color: black;
  max-width: 80%;
  width:auto;
  word-wrap: break-word;
}

.fade-in-job.edu {
  transform: translateX(20vh);
}

.fade-in-job.is-visible {
  opacity: 1;
  transform: translateX(0);
  visibility: visible;
}

.workContainer {
  grid-column: 1 / span 6;
  text-align: center;
  margin-left: auto;
  margin-right: auto;
}

.workHeader {
  text-shadow: 2px 2px #14213d;
  font-weight: bold;
  color: #fca311;
  font-size: 2em;
  margin: 1vh;
}

.fade-in-work {
  opacity: 0;
  transform: translateX(-20vh);
  visibility: hidden;
  transition: opacity 0.6s ease-out, transform 0.25s ease-out;
  will-change: opacity, visibility;
}

.fade-in-work.is-visible {
  opacity: 1;
  transform: translateY(0);
  visibility: visible;
}

@media only screen and (max-width: 1000px) {
  .workContainer {
    grid-column: 1 / span 12;
  }
}

.eduContainer {
  grid-column: 7 / 12;
  overflow: hidden;
  text-align: center;
  margin-left: auto;
  margin-right: auto;
}

.fade-in-edu {
  opacity: 0;
  transform: translateX(20vh);
  visibility: hidden;
  transition: opacity 0.6s ease-out, transform 0.25s ease-out;
  will-change: opacity, visibility;
}

.fade-in-edu.is-visible {
  opacity: 1;
  transform: translateY(0);
  visibility: visible;
}

@media only screen and (max-width: 1000px) {
  .eduContainer {
    grid-column: 1 / span 12;
    margin-left: auto;
    margin-right: auto;
  }
}

.toolsContainer {
    grid-row: 2;
    grid-column: 6 / span 12;
    text-align: center;
    margin: 2em;
}

.toolsHeader {
    text-shadow: 2px 2px #14213d;
    font-weight: bold;
    color: #fca311;
    font-size: 2em;
    margin-bottom: 1em;
}

.toolLists {
    display: flex;
    flex-direction: row;
    width: 100%;
    justify-content: space-between;
    background-color: #efefef;
    box-shadow: 2px 2px 19px 8px rgba(30, 30, 60, 0.37);
    border-radius: 15px;
    padding: 1em 2em 0 2em;
    
}

.toolSection:not(:last-child) {
    margin-right: 2em;
}

.toolListHeader {
    display: block;
    font-weight: bold;
    color: #14213d;
    font-size: 1.5em;
    font-weight: 600;
    margin-bottom: 0.5em;
    white-space: nowrap;
}

.toolList {
    margin: 0 0 1.5em;
}

.tech {
    display: flex;
    align-items: center;
    justify-content: flex-start;
    font-size: 1.25em;
    height: 2em;
    margin-bottom: 0.5em;
}
.techLogo {
    width: 2em;
    margin-right: 0.5em;
    min-width: 2em;
}

@media only screen and (max-width: 1000px) {
    .toolsContainer{
        grid-column: 1 / span 12;
        grid-row: 4;

        margin-left: auto;
        margin-right: auto;
    }
    
}

@media only screen and (max-width: 550px) {

    .toolsContainer {
        min-width: 70%;
    }

    .toolSection:not(:last-child) {
        margin: 0;
        margin-bottom: 1em;
    }

    .tech {
        width: -webkit-max-content;
        width: -moz-max-content;
        width: max-content;
        margin-left: 20%;
        margin-right: auto;
    }
    .toolLists {
        flex-direction: column;
        padding: 0;
        padding-top: 1em
    }
}

footer {
    grid-row: auto;
    grid-column: 1 / 13;
    background: rgb(40, 44, 46);
    height: auto;
    padding: 1em 2em 1em 2em;
    color: white;
    font-size: 1.1em;
}

.footerUpper {
    display: flex;
    flex-direction: row;
    overflow: hidden;
    justify-content: space-between;
    
}

footer > p {
    margin-bottom: 1em;
}

.hiring {
    max-width: 50%;
}

.hiringHeader {
    font-weight: bold;
    width: 100%;
    color: #fca311;
    font-size: 1.5em;
    text-align: center;
    margin-bottom: 0.5em;
}

.hiringText {
    display: block;
}

.emailText {
    margin-top:auto;
    margin-bottom: auto;
    margin-left: 2em;
    max-width: 50%
}

.email {
    color: #fca311;
    transition: 0.1s ease-in-out;
}

.email:active{
    color: white
}
.email:hover{
    color: white;
}

.email:visited {
    color: #fca311;
}

.copyright {
    width: 100%;
    text-align: center;
    display: block;
    margin-top: 1.5em;
    color: #fca311;
}

@media only screen and (max-width: 550px) {
    .footerUpper {
        flex-direction: column;
    }

    .hiring {
        max-width: 100%;
    }

    .emailText {
        margin: 1em 0 0 0;
        max-width: 100%;
    }
}
